export default {
  layout: 'hero',

  data: () => ({
    blur: true,
    loading: true
  }),
  computed: {
    show: {
      get () {
        return !this.loading
      },
      set () {}
    }
  },
  mounted () {
    setTimeout(() => (this.blur = false), 0)
    setTimeout(() => (this.loading = false), 500)
  }
}
